h1 {
    font-size: 90px;
    margin: 0;
    color: $maroon;
    text-shadow: $text-shadow;
    @media (max-width: 768px) {
        font-size: 60px;
    }
}
h2 {
    font-size: 50px;
    margin: 0 0 24px 0;
    color: $maroon-dk;
    text-shadow: $text-shadow;
    span {
    	color: $yellow-lt;
    	text-transform: uppercase;
    }
    @media (max-width: 768px) {
        font-size: 32px;
    }
}