input {
	padding: 20px;
	font-family: $display;
	border: 4px solid $yellow-lt;
	font-size: 20px;
	background-color: $yellow-lt;
	color: $yellow-lt;
	border-radius: 12px;
	box-shadow: $box-shadow;

	&:focus {
		border-color: $purple;
	}
}
button {
	padding: 24px 35px;
	font-family: $display;
	transition: all .2s;
	border-radius: 8px;
	border: none;
	background-color: $purple;
	color: white;
	font-weight: bold;
	font-size: 18px;
	box-shadow: $box-shadow;

	&:hover {
		cursor: pointer;
		background-color: $purple-lt;
	}
}
