/************************************************************************************************************
screens
*************************************************************************************************************/
.screen {
	position: relative;
	height: 100vh;
	width: 100vw;
	display: flex;
	align-items: center;
	justify-content: center;

	&.gameover-screen {
		display: none;
	}

	&.lost h2 {
		cursor: pointer;

		&:hover {
			color: $yellow-lt;
		}
		&.revealed {
			color: $yellow-lt;
			text-transform: uppercase;
		}
	}

	&.timeout h2 {
		max-width: 600px;
		margin: 24px auto;
	}

	&.timeout h3 {
		margin: 24px auto 36px auto;
		font-size: 36px;
		cursor: pointer;
		&:hover {
			color: $yellow-lt;
		}
		&.revealed {
			color: $yellow-lt;
			text-transform: uppercase;
		}
	}

	.screen-contents {
		width: 96%;
		//max-width: 900px;
		text-align: center;
		margin-top: -100px;

        /*height*/
        @media (max-height:900px) {
            margin-top: 0;
            padding-top: 50px;
        }
	}
    &.start-screen .screen-contents {
        /*height*/
        @media (max-height:900px) {
            padding-top: 0;
        }
        @media (max-width:768px) {
            padding-top: 0;
        }
    }
}

/************************************************************************************************************
start options
*************************************************************************************************************/
.start-option {
	color: $purple;

	&:before {
		content: "";
		display: block;
		width: 300px;
		height: 4px;
		border-radius: 4px;
		margin: 12px auto;
		background-color: $maroon;
	}

    #clear-word-input {
        display: inline-block;
        font-family: helvetica, arial, sans-serif;
        font-size: 16px;
        color: black;
        text-decoration: underline;
        &:hover {color: $purple; cursor: pointer;}
        padding-top: 8px;
    }
}

/************************************************************************************************************
word input (start screen)
*************************************************************************************************************/
.word-input {
	.word {
		margin: 0 6px 24px 0;
	}
}

/************************************************************************************************************
guesses
*************************************************************************************************************/
.guess-input {
	display: block;
	opacity: 0;

	.letter {
        height: 0;
        padding: 0;
		/*max-width: 26px;
		border: 3px solid $purple;
		border-radius: 6px;
		color: black;
		text-transform: uppercase;
		font-family: $display;*/
	}

}

.strikes {
	border-radius: 4px;
	text-transform: uppercase;
	font-size: 40px;
	font-weight: bold;
	color: firebrick;
	margin-top: 36px;
	width: 100%;
	text-align: center;
	color: $maroon;
	height: 54px;
}

.word-reveal {
	margin-bottom: 36px;
	text-align: center;
	//margin-left: 36px;

	.letter-box {
		padding: 4px 6px;
		width: 100px;
		height: 112px;
		text-align: center;
		vertical-align: middle;
		font-weight: bold;
		color: $purple;
		font-size: 80px;
		text-transform: uppercase;
		background-color: $yellow-lt;
		border-radius: 16px;
		display: inline-block;
		margin-right: 10px;
		color: $purple;
		box-shadow: $box-shadow;

		&.unknown {
			background-color: $purple;

			& > * {
				display: none;
			}
		}
	}

	&.flexible {
        position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-wrap: nowrap;

		.letter-box {
            height: 60px;
			font-size: 50px;
			//width: auto;
			//height: auto;
		}
	}

    /*height*/
    @media (max-height:900px) {
        margin: 0;
    }
}

/************************************************************************************************************
stats bar
*************************************************************************************************************/
.stats-bar {
	position: absolute;
	top: 0;
	left: 0;
	padding: 12px 24px;
	width: 100%;
	color: $white;
	background-color: $maroon-dk;
	text-align: left;
	box-shadow: $box-shadow;

	.stat {
		margin-right: 24px;
		display: inline-block;
		min-width: 150px;

		& > * {
			display: inline-block;
		}
		.data {
			color: $yellow;

			&.emergency {
				color: $red;
			}
		}
	}
}

/************************************************************************************************************
stick man
*************************************************************************************************************/
.gallows {
	width: 300px;
	height: 285px;
	position: relative;
	margin: 0 auto 36px auto;
	overflow: hidden;
}

.body-part {
	background-color: $maroon;
	width: 12px;
	position: absolute;
	opacity: 0;
	transition: all .2s;
	border-radius: 6px;

	&.show {
		opacity: 1;
	}
}

.head {
	width: 25px;
	height: 25px;
	left: 50%;
	margin-left: -25px;
	top: 30px;
	border-radius: 40px;
	background: none;
	border: 12px solid $maroon;
}

.body {
	height: 100px;
	top: 73px;
	left: 50%;
	margin-left: -6px;
}

.lt-arm {transform: rotate(70deg);  height: 70px; top: 76px;  left: 110px;}
.rt-arm {transform: rotate(-70deg); height: 70px; top: 76px;  left: 178px;}
.lt-leg {transform: rotate(24deg);  height: 70px; top: 159px; left: 131px;}
.rt-leg {transform: rotate(-24deg); height: 70px; top: 159px; left: 157px;}
$pole-thickness : 12px;
.pole {background-color: $maroon-dk; position: absolute; border-radius: 6px;}
.pole.noose  {width: $pole-thickness;   height: 24px;  top: 10px;  left: 144px;}
.pole.top    {width: 113px; height: $pole-thickness;   top: 10px;  left: 40px;}
.pole.side   {width: $pole-thickness;   height: 266px; top: 10px;  left: 40px;}
.pole.bottom {width: 89px; height: $pole-thickness;    top: 270px; left: 0;}

/************************************************************************************************************
instructions
*************************************************************************************************************/
.instructions {
	background-color: $maroon-dk;
	color: $white;
	border-radius: 8px;
	padding: 9px 45px;
	position: relative;
	max-width: 400px;
    margin: auto;

	p {
		margin: 0;

		&:last-of-type {
			font-size: 70%;
			opacity: .7;
			margin-top: 6px;
		}
	}

    
}