html {
	margin: 0;
	padding: 0;
	width: 100%;
	height: 100%;
	position: relative;
}
body {
	font-family: $display;
	font-size: 20px;
	background-image: url('assets/img/background-west-desert.jpg');
	background-position: center bottom;
	background-size: cover;
	position: relative;
	width: 100%;
	height: 100%;
	margin: 0;
	padding: 0;
	overflow: hidden;
}
*,*:focus,*:hover {
	outline:none;
}
::placeholder {
	color: $black;
}
footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 8px 0;
    background-color: rgba(0, 0, 0, .7);
    text-align: center;
    font-family: helvetica, arial, sans-serif;
    font-size: 13px;
    a {color: #fff; text-decoration: none; &:hover{text-decoration: underline;}}
}